<template>
    <el-col :span="20">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
            <el-form-item label="题目名称" prop="title">
                <el-input v-model="ruleForm.title"></el-input>
            </el-form-item>
            <el-form-item label="题目难度">
                <el-rate v-model="ruleForm.difficulty" v-bind:max="max" show-text :texts="['简单', '容易', '较难']"></el-rate>
            </el-form-item>
            <el-form-item label="题目答案">
            </el-form-item>
            <el-form-item label="A" prop="answerList[0].answer">
                <el-row :gutter="20">
                    <el-col :span="12">
                        <el-input v-model="ruleForm.answerList[0].answer"></el-input>
                    </el-col>
                    <el-col :span="10">
                        <el-checkbox :true-label="1" :false-label="0" name="type" v-model="ruleForm.answerList[0].isRightAnswer"></el-checkbox>
                    </el-col>
                </el-row>
            </el-form-item>
            <el-form-item label="B" prop="answerList[1].answer">
                <el-row :gutter="20">
                    <el-col :span="12">
                        <el-input v-model="ruleForm.answerList[1].answer"></el-input>
                    </el-col>
                    <el-col :span="10">
                        <el-checkbox name="type" :true-label="1" :false-label="0" v-model="ruleForm.answerList[1].isRightAnswer"></el-checkbox>
                    </el-col>
                </el-row>
            </el-form-item>
            <el-form-item label="C" prop="answerList[2].answer">
                <el-row :gutter="20">
                    <el-col :span="12">
                        <el-input v-model="ruleForm.answerList[2].answer" ></el-input>
                    </el-col>
                    <el-col :span="10">
                        <el-checkbox name="type" :true-label="1" :false-label="0" v-model="ruleForm.answerList[2].isRightAnswer"></el-checkbox>
                    </el-col>
                </el-row>
            </el-form-item>
            <el-form-item label="D" prop="answerList[3].answer">
                <el-row :gutter="20">
                    <el-col :span="12">
                        <el-input v-model="ruleForm.answerList[3].answer"></el-input>
                    </el-col>
                    <el-col :span="10">
                        <el-checkbox name="type" :true-label="1" :false-label="0" v-model="ruleForm.answerList[3].isRightAnswer"></el-checkbox>
                    </el-col>
                </el-row>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')">{{addTitle}}</el-button>
                <el-button @click="resetForm('ruleForm')">重置</el-button>
            </el-form-item>
        </el-form>
    </el-col>
</template>

<script>
    export default {
        name:'addTopic',
        mounted(){
            let self = this
            let data = self.$route.query.data;
            if(data){
                self.addTitle = '立即修改'
                self.ruleForm = data
                self.method = 'PUT'
            }else{
                self.method = 'POST'
            }
        },
        data() {
            return {
                addTitle:'立即添加',
                max:3,
                ruleForm: {
                    title: '',
                    difficulty: 1,
                    answerList:[
                        {
                            answer:'',
                            isRightAnswer:0
                        },
                        {
                            answer:'',
                            isRightAnswer:0
                        },
                        {
                            answer:'',
                            isRightAnswer:0
                        },
                        {
                            answer:'',
                            isRightAnswer:0
                        }

                    ]
                },
                rules: {
                    title: [{
                        required: true, message: '请输入题目名称', trigger: 'blur'
                    }],
                    answerList:[
                        {
                            answer:{required: true, message: '请输入答案名称', trigger: 'blur'}
                        },
                        {
                            answer:{required: true, message: '请输入答案名称', trigger: 'blur'}
                        },
                        {
                            answer:{required: true, message: '请输入答案名称', trigger: 'blur'}
                        },
                        {
                            answer:{required: true, message: '请输入答案名称', trigger: 'blur'}
                        },
                    ]
                }
            };
        },
        methods: {
            submitForm(formName) {
                var self = this
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        self.$ajax({url:'topic/',data:self.ruleForm,method:self.method}).then(function (response) {
                            if(response.code === 1){
                                self.$notify({
                                    title: '成功',
                                    message: '题目添加成功',
                                    type: 'success',
                                    duration:1000
                                });
                            }
                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }
        }
    }
</script>

<style lang="stylus">
    .el-rate
        display inline-block
        vertical-align middle

    .el-row
        margin-bottom 20px
</style>





